<template>
  <div class="header">
    <slot></slot>
    <i class="iconfont  icon-fanhui" v-if="back" @click="goback"></i>
  </div>

</template>
<script>
  export default {
    props:{
      back:{
        type:Boolean,
        default:false
      }
    },
    data(){
      return {}
    },
    created(){},
    methods:{
      goback(){
        this.$router.go(-1);
      }
    },
    computed:{},
    components:{}
  }
</script>
<style scoped lang="less">
  .header{
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 40px;
    text-align: center;
    line-height: 40px;
    font-size: 20px;
    background: #F6C6CE;
    font-weight: bold;
    letter-spacing: 3px;
    color: #880000;
    i{
      position: absolute;
      font-size: 30px;
      left:10px;
      top:5px;
    }
  }
</style>



